@use 'variable' as var;


@mixin telefono {
    @media (min-width: 480px) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: 768px) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: 992px) {
        @content;
    }
}


@mixin desktop-1xl {
    @media (min-width: 1200px) {
        @content;
    }
}


@mixin desktop-2xl {
    @media (min-width: 1400px) {
        @content;
    }
}

@mixin desktop-3xl {
    @media (min-width: 1980px) {
        @content;
    }
}

@mixin grid ($columnas, $espacio){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espacio;
}

@mixin reset-list {
    margin: 0;
    padding: 0;
    list-style: none;
}