@use 'variable' as var;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-size: 1.6rem;
    /* 1rem = 10px */
    line-height: 1.5;
    font-family: var.$fuente_principal;

    &.fijar-body {
        overflow: hidden;
    }

    &.body-scroll {
        padding-top: 8.6rem;
    }
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}

p {
    color: var.$negro;
    font-size: 2rem;
    line-height: 1.5;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}

h1,
h2,
h3 {
    margin: 0 0 5rem 0;
}