.hero {
  height: 400px;
  width: 100%;
  background: #F5F7F8;
  background-image: url(../img/hero.webp);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero .header-hero {
  margin: 1rem;
  text-align: center;
  background: rgba(245, 247, 248, 0.6);
  padding: 1.4rem;
  border-radius: 2rem;
}
.hero .header-hero .titulo {
  font-size: 1.7rem;
  font-weight: 700;
}
.hero .header-hero .parrafo {
  font-size: 1.5rem;
  text-align: justify;
  line-height: 1.3;
}

.buscador {
  text-align: center;
  padding: 5rem 0;
  background: #F5F7F8;
}
.buscador h3 {
  margin: 0;
  font-size: 1.9rem;
}
.buscador .search {
  width: 90%;
  padding: 1rem;
  border-radius: 1.2rem;
  margin-top: 1.3rem;
}
@media (min-width: 480px) {
  .buscador .search {
    width: 80%;
  }
}
@media (min-width: 768px) {
  .buscador h3 {
    font-size: 2.3rem;
  }
  .buscador .search {
    width: 70%;
  }
}
@media (min-width: 992px) {
  .buscador h3 {
    font-size: 2.7rem;
  }
  .buscador .search {
    width: 50%;
  }
}

.categorias {
  margin: 2rem 0;
}
.categorias .header-title {
  padding: 0.5rem 1rem;
}
.categorias .header-title h2 {
  color: #001588;
  font-size: 2rem;
  margin: 0;
}
.categorias .header-title p {
  margin: 0;
  font-size: small;
  color: #001588;
  font-weight: 500;
}
@media (min-width: 480px) {
  .categorias .header-title h2 {
    font-size: 2.5rem;
  }
  .categorias .header-title p {
    font-size: 1.7rem;
  }
}
.categorias .grupo-categorias-destacadas {
  margin: 1rem;
  padding: 1rem;
}
@media (min-width: 768px) {
  .categorias .grupo-categorias-destacadas {
    padding: 0.5rem 2.5rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    text-align: center;
    justify-content: center;
    align-content: center;
  }
}
@media (min-width: 992px) {
  .categorias .grupo-categorias-destacadas {
    padding: 0.5rem 2.5rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    text-align: center;
    justify-content: center;
    align-content: center;
  }
}
@media (min-width: 1400px) {
  .categorias .grupo-categorias-destacadas {
    padding: 0.5rem 2.5rem;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
    text-align: center;
    justify-content: center;
    align-content: center;
  }
}
.categorias .card {
  border-radius: 1.2rem !important;
  background: #F5F7F8;
  margin-bottom: 1.7rem;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px;
  display: flex;
  flex-direction: row;
}
@media (min-width: 768px) {
  .categorias .card {
    flex-direction: column;
  }
}
.categorias .card .card-header {
  visibility: hidden;
  width: 0;
}
.categorias .card .card-body {
  padding: 1rem;
}
.categorias .card .card-body .titulo {
  font-weight: 700;
}
.categorias .card:hover {
  box-shadow: rgba(0, 0, 0, 0.9) 0px 1px 4px;
  cursor: pointer;
}
.categorias .card:hover img {
  opacity: 1;
}
@media (min-width: 480px) {
  .categorias .card .card-header {
    width: 40%;
    visibility: visible;
  }
  .categorias .card .card-header img {
    border-radius: 2.2rem 0 0 1.2rem;
    opacity: 0.8;
  }
  .categorias .card .card-body {
    width: 60%;
    padding: 1rem;
    font-size: 1.7rem;
  }
  .categorias .card .card-body .titulo {
    font-weight: 700;
  }
  .categorias .card .card-body .cuerpo {
    font-size: 1.4rem;
  }
}