.hero {
    height: 400px;
    width: 100%;
    background: #F5F7F8;
    background-image: url(../img/hero.webp);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .header-hero {
        margin: 1rem;
        text-align: center;
        background: rgba(245, 247, 248, .6);
        padding: 1.4rem;
        border-radius: 2rem;

        .titulo {
            font-size: 1.7rem;
            font-weight: 700;
        }

        .parrafo{
            font-size: 1.5rem;
            text-align: justify;
            line-height: 1.3;
        }
    }
}


