.categorias {
    margin: 2rem 0;

    .header-title {
        padding: .5rem 1rem;
        h2 {
            color: #001588;
            font-size: 2rem;
            margin: 0;
        }

        p {
            margin: 0;
            font-size: small;
            color: #001588;
            font-weight: 500;
        }

        @include telefono {
            h2 {
                font-size: 2.5rem;
            }

            p{
                font-size: 1.7rem;
            }
        }
    }


    .grupo-categorias-destacadas {
        margin: 1rem;
        padding: 1rem;

        @include tablet {
            padding: .5rem 2.5rem;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 2rem;
            text-align: center;
            justify-content: center;
            align-content: center;
        }

        @include desktop {
            padding: .5rem 2.5rem;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 2rem;
            text-align: center;
            justify-content: center;
            align-content: center;
        }

        @include desktop-2xl {
            padding: .5rem 2.5rem;
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            gap: 2rem;
            text-align: center;
            justify-content: center;
            align-content: center;

        }
    }


    .card {
        border-radius: 1.2rem !important;
        background: #F5F7F8;
        margin-bottom: 1.7rem;
        box-shadow: rgba(0, 0, 0, .3) 0px 1px 4px;

        display: flex;
        flex-direction: row;

        @include tablet {
            flex-direction: column;
        }

        .card-header {
            visibility: hidden;
            width: 0;
        }

        .card-body {
            padding: 1rem;

            
            .titulo {
                font-weight: 700;
            }
        }


        &:hover {
            box-shadow: rgba(0, 0, 0, .9) 0px 1px 4px;
            cursor: pointer;

            img {
                opacity: 1;
            }
        }

        @include telefono {
            .card-header {
                width: 40%;
                visibility: visible;

                img {
                    border-radius: 2.2rem 0 0 1.2rem;
                    opacity: .8;
                }
            }

            .card-body {
                width: 60%;
                padding: 1rem;
                font-size: 1.7rem;

                .titulo {
                    font-weight: 700;
                }

                .cuerpo {
                    font-size: 1.4rem;
                }
            }

        }


    }



}