.buscador {
    text-align: center;
    padding: 5rem 0;
    background: #F5F7F8;

    h3 {
        margin: 0;
        font-size: 1.9rem;
    }

    .search {
        width: 90%;
        padding: 1rem;
        border-radius: 1.2rem;
        margin-top: 1.3rem;
    }

    @include telefono {
        .search {
            width: 80%;
        }
    }

    @include tablet {

        h3 {
            font-size: 2.3rem;
        }

        .search {
            width: 70%;
        }
    }

    @include desktop {

        
        h3 {
            font-size: 2.7rem;
        }

        .search {
            width: 50%;
        }
    }


}
